export default {
	data() {
		return {
			detail:[],
		}
	},
	onLoad(e) {
		var t=this;	
		uni.showLoading();
		t.urlRequest.urlRequest(t.func.getWorkLogDetail,{worklog_id:e.id},function(res){
			
			t.detail=res.data.worklog_row
			if(t.detail.file){
				for(var i=0;i<t.detail.file.length;i++){
					t.detail.file[i]['upload_path']=t.sta.siteroot+'/uploads/'+t.detail.file[i]['upload_path'];
				}
			}
		})
		uni.hideLoading();
	},
	methods: {
		download(index) {
			var _this = this
			uni.downloadFile({
				url: index, //仅为示例，并非真实的资源
				success: (res) => {
					var path = res.tempFilePath
					console.log(path);
					if (res.statusCode === 200) {
						// preview
						uni.openDocument({
							filePath: path,
							success: function(res) {
								console.log('打开文档成功');
								console.log(res);
							}
						});
						//文件保存
						uni.saveFile({
							tempFilePath: path,
							success: function(res) {
								var savedFilePath = res.savedFilePath;
								console.log(savedFilePath)
								uni.showToast({
									title: '下载成功',
									content: savedFilePath,
									duration: 500
								});
								uni.getSavedFileList({
									success: function(res) {
										console.log(res.fileList);
									}
								});
							}
						});
					}
				}
			});
		},
		activeDetail:function(id){
			uni.navigateTo({
				url: '../index1/active-detail?id='+id
			})
		},
	}
}
